import { FC } from 'react';
import Map from './views/Map';
import NavBar from './components/NavBar';
import AddPoint from './views/AddPoint';
import { Route, Switch } from 'react-router';
import { BrowserRouter } from 'react-router-dom';

const App: FC = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/">
                    <NavBar />
                    <Map />
                </Route>
                <Route path='/addpoint'>
                    <AddPoint/>
                </Route>
            </Switch>
        </BrowserRouter>
    );
};
export default App;
