import { FC } from "react";
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import { testdata } from "../test-data/testdata";

const Map: FC = () => {
    const polandCenter:Array<number> = [52.0409, 19.2849]

    return (
        <div id="mapid">
            <MapContainer center={[52.0409, 19.2849]} zoom={8} scrollWheelZoom={false}>
                <TileLayer
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                {testdata.map((data) => (
                    <Marker position={[data.cordX, data.cordY]}>
                        <Popup>
                            <h2>{data.name}</h2>
                            <p>{data.description}</p>
                            <p>{data.cordX} {data.cordY}</p>
                            <a href={"https://maps.google.com/maps/place/" + data.cordX + "," + data.cordY}>
                                <button>Otwórz w Google Maps</button>
                            </a>
                        </Popup>
                    </Marker>
                ))}
            </MapContainer>
        </div>
    );
};
export default Map;